@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  min-height: 100vh;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #E4E8EB;
}

nav,
footer.page-footer {
  background-color: #040404;
}

main {
  -webkit-box-flex: 1;
      -ms-flex: 1 0 auto;
          flex: 1 0 auto;
}

stk-header nav {
  height: 104px;
}

stk-header .nav-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

stk-header .brand-logo i {
  font-size: 3em;
}

stk-header .menu,
stk-header .menu-mobile {
  text-transform: uppercase;
}

stk-header .button-collapse {
  cursor: pointer;
}

stk-footer {
  color: white;
}

stk-footer .social-links {
  text-align: right;
}

stk-footer a.social-link {
  padding: 0 10px;
  color: inherit;
  text-decoration: none;
  font-size: 3em;
}

stk-footer a.social-link:hover {
  color: #950000;
}

